import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { first } from 'rxjs/operators';
import { AuthenticationService } from '../services/authentication.service';
import { threadId } from 'worker_threads';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  loginForm: FormGroup;
  submitted = false;
  error = '';
  loading = false;
  returnUrl: string;
  accessToken: any;

  constructor(private formBuilder:FormBuilder,private route: ActivatedRoute,
    private router: Router,private httpService:AuthenticationService) { }

  ngOnInit(){

    this.loginForm = this.formBuilder.group({
      username:['',[Validators.required,]],
      password:['', [Validators.required, Validators.minLength(4)]]
   });
  // this.httpService.navigateToDashboardIfUserExists();
   //this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/';

  }
  get f() { return this.loginForm.controls; }

  getUserDetails(){
    // const currentUser = localStorage.getItem( 'currentUser' );
    // this.accessToken = JSON.parse( currentUser )['Token'];
    this.httpService.getUserDetails(this.accessToken)
    .subscribe({
      next:(data) => {
        console.log(data);
        //superadmin
        if(data.role === 1){
          this.router.navigate(['/home/super_admin'])
        }
        //admin
        else if(data.role === 2){
          this.router.navigate(['admin/dashboard'])
        }
        //vendor&user
        else if(data.role === 3){
          this.router.navigate(['/home/vendor'])
        }
        //user
        else if(data.role === 4){
          this.router.navigate(['/home/fashion'])
        }
        else{
          console.log("role not defined")
        }
        
      },
      error:(error)=>{
      this.error = error;
      console.error(error)
      this.loading = false;
  }
  });
  }

  onSubmit() {
    this.submitted = true;
    if (this.loginForm.invalid) {
        return;
    }
    this.loading = true;
    this.httpService.login(this.f.username.value, this.f.password.value)
    .subscribe({
      next:(data) => {
        console.log(data);
        
    const userToken = { Token: data.accessToken,};
    this.accessToken = data.accessToken;
    this.httpService.accessToken=data.accessToken
    localStorage.clear();

    localStorage.setItem('currentUser', JSON.stringify(userToken));

    this.getUserDetails();
    // const currentUser = localStorage.getItem( 'currentUser' );
    // const token = JSON.parse( currentUser ).access_token;
    // this.userService.redirectPage(token)
        // user.authdata = window.btoa(username + ':' + password);
         //     localStorage.setItem('user', JSON.stringify(user));
         //     this.userSubject.next(user);
         //     console.log(user);
         //     return user;
        
      
      },
      error:(error)=>{
       this.error = error;
       console.error(error)
       this.loading = false;
   }
  });
        // console.log(results);
        //console.log(this.user);
        //alert('SUCCESS!! :-)\n\n' + JSON.stringify(this.loginForm.value))
}

}
